import React from "react"
import {
    Button
} from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"
import Can from '../../../../../utility/permissions/can';

class FiscalYearTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Can I="update" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleEdit(row)}
                                >
                                    <Edit size={14} />
                                </Button.Ripple>
                            }
                        </Can>

                        <Can I="delete" a={this.props.role} passThrough>
                            { allowed => 
                                <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                                    disabled={!allowed}
                                    onClick={() => this.props.handleDelete(row)}
                                ><Trash2 size={14} />
                                </Button.Ripple>
                            }
                        </Can>
                    </>
                )
            },
            {
                name: "Fiscal Year Name",
                selector: "FISCALYEARNAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.YEAR_NAME}</p>
            },
            {
                name: "StartDate",
                selector: "STARTDATE",
                sortable: true,
                cell: row => <p className="mb-0">{row.START_DATE}</p>
            },
            {
                name: "EndDate",
                selector: "ENDDATE",
                sortable: true,
                cell: row => <p className="mb-0">{row.END_DATE}</p>
            },
            {
                name: "Date Initialised",
                selector: "DATEINITIALISED",
                sortable: true,
                cell: row => <p className="mb-0">{row.DATE_INITIALIZED}</p>
            },
            {
                name: "Date Closed",
                selector: "DATECLOSED",
                sortable: true,
                cell: row => <p className="mb-0">{row.DATE_CLOSED}</p>
            },

            // {
            //     name: "Closed By",
            //     selector: "CLOSEDBYID",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.CLOSEDBY}</p>
            // },
            // {
            //     name: "Current Year",
            //     selector: "CURRENTYEAR",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.CURRENTYEAR}</p>
            // },
            // {
            //     name: "Opened",
            //     selector: "OPENED",
            //     sortable: true,
            //     cell: row => <p className="mb-0">{row.OPENED}</p>
            // },
            {
                name: "No Of Periods",
                selector: "NOOFPERIODS",
                sortable: true,
                cell: row => <p className="mb-0">{row.NO_OF_PERIODS}</p>
            },
            {
                name: "Status",
                selector: "SYS_STATUS_NAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.SYSTEMSTATUS.SYS_STATUS_NAME}</p>
            }
        ]
    }

    render() {

        return (
            <>
                <DataTable
                    className="dataTable-custom"
                    data={this.props.value.length ? this.props.filteredData : this.props.data}
                    columns={this.tableProperties.columns}
                    noHeader
                    pagination
                    subHeader
                    striped
                    subHeaderComponent={
                        <TableCustomHeader role={this.props.role} value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                    }
                />
            </>

        )
    }
}


export default FiscalYearTable