import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import FiscalYearTable from './Tables/FiscalYearTable';
import FiscalYearModal from './Modals/FiscalYearModal'
import * as API from "../../../../utility/crud/app.crud";
import * as _Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';



class FiscalYear extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            validated: false,
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ID: 0,
            FISCALYEARNAME: '',
            STARTDATE:new Date(),
            ENDDATE:new Date(),
            DATEINITIALISED:new Date(),
            DATECLOSED:new Date(),
            INITIALISEDBYID:1,
            CLOSEDBYID:1,
            CREATEDBYID: 1,
            DATECREATED: _Date.ShowDate(),
            LASTMODIFIEDBYID: 1,
            DATELASTMODIFIED: _Date.ShowDate(),
            CURRENTYEAR: 1,
            OPENED: 1,
            NOOFPERIODS: 1,
            InsertUpdate: '',
            opensearch: false,
            SYSTEMSTATUSTYPEDESCRIPTION: '',

            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',
            SYS_STATUS_ID: 0,
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }
        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };
        this.populateData();
        this.populateTypes() ;
    }

    populateData() {
        const getdata = `
            query{
                FiscalYears{
                    YEAR_ID
                    YEAR_NAME
                    START_DATE
                    END_DATE
                    DATE_INITIALIZED
                    DATE_CLOSED
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CLOSEDBY
                    CREATEDBY
                    CREATED_DATE
                    MODIFIEDBY
                    MODIFIED_DATE
                    CURRENTYEAR
                    OPENED
                    NO_OF_PERIODS
                }
            }
        `;
    
        API.getData(getdata)
        .then(({ data }) => this.setState({
            data: data.data.FiscalYears
        })).catch(error => {
            if (error.response || error.request) {
                toastr.error('Error Loading Data', 'Failed to Load Data');
            }
        })

    }
    populateTypes() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
        .then(({ data }) => this.setState({
            searchdata: data.data.SystemStatusFilter
        })).catch(error => {
            if (error.response || error.request) {
                toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
            }
        })
    }
    handleSubmit = (event) => {
        event.preventDefault();
        const validate = event.currentTarget;
        if (validate.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        }
        else {

        const form = `
        mutation{
            addFiscalYear(
                YEAR_NAME:"${this.state.FISCALYEARNAME}",
                START_DATE:"${this.state.STARTDATE}",
                END_DATE:"${this.state.ENDDATE}",
                DATE_INITIALIZED:"${this.state.DATEINITIALISED}",
                DATE_CLOSED:"${this.state.DATECLOSED}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CLOSEDBY:${this.state.CLOSEDBYID},
                CREATEDBY:${this.state.CREATEDBYID},
                CREATED_DATE:"${this.state.DATECREATED}",
                MODIFIEDBY:${this.state.LASTMODIFIEDBYID},
                MODIFIED_DATE:"${this.state.DATELASTMODIFIED}",
                CURRENTYEAR:${this.state.CURRENTYEAR},
                OPENED:${this.state.OPENED},
                NO_OF_PERIODS:${this.state.NOOFPERIODS}
                ){
                    YEAR_ID
            }
        }
        `;
        
        const formEdit = `
        mutation{
            updateFiscalYear(
                YEAR_ID:${this.state.FISCALYEARID},
                YEAR_NAME:"${this.state.FISCALYEARNAME}",
                START_DATE:"${this.state.STARTDATE}",
                END_DATE:"${this.state.ENDDATE}",
                DATE_INITIALIZED:"${this.state.DATEINITIALISED}",
                DATE_CLOSED:"${this.state.DATECLOSED}",
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                CLOSEDBY:${this.state.CLOSEDBYID},
                CREATEDBY:${this.state.CREATEDBYID},
                CREATED_DATE:"${this.state.DATECREATED}",
                MODIFIEDBY:${this.state.LASTMODIFIEDBYID},
                MODIFIED_DATE:"${this.state.DATELASTMODIFIED}",
                CURRENTYEAR:${this.state.CURRENTYEAR},
                OPENED:${this.state.OPENED},
                NO_OF_PERIODS:${this.state.NOOFPERIODS}
                ){
                    YEAR_ID
            }
        }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }
    }
    this.setState({
        validated: true
    })
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteFiscalYear(
                YEAR_ID:${this.state.FISCALYEARID}
                ){
                    YEAR_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }
    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });
    };
    handleEditData = (e) => {
        this.setState({
            FISCALYEARID: e.YEAR_ID,
            FISCALYEARNAME: e.YEAR_NAME,
            STARTDATE:e.START_DATE,
            ENDDATE:e.END_DATE,
            DATEINITIALISED:e.DATE_INITIALIZED,
            DATECLOSED:e.DATE_CLOSED,
            // INITIALISEDBYID:e.INITIALISEDBYID,
            CLOSEDBYID:e.CLOSEDBY,
            CREATEDBYID:e.CREATEDBY,
            DATECREATED:e.CREATED_DATE,
            LASTMODIFIEDBYID:e.MODIFIEDBY,
            DATELASTMODIFIED:e.MODIFIED_DATE,
            CURRENTYEAR:e.CURRENTYEAR,
            OPENED:e.OPENED,
            NOOFPERIODS:e.NO_OF_PERIODS,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION:e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            FISCALYEARID: e.YEAR_ID,
            FISCALYEARNAME: e.YEAR_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            FISCALYEARNAME: '',
            STARTDATE:new Date(),
            ENDDATE:new Date(),
            DATEINITIALISED:new Date(),
            DATECLOSED:new Date(),
            INITIALISEDBYID:1,
            CLOSEDBYID:1,
            CREATEDBYID: 1,
            DATECREATED: _Date.ShowDate(),
            LASTMODIFIEDBYID: 1,
            DATELASTMODIFIED: _Date.ShowDate(),
            CURRENTYEAR: 1,
            OPENED: 1,
            NOOFPERIODS: 1,
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION:'',
            validated: false
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.YEAR_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.YEAR_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }
    render() {
        const { data, columns, value, filteredData, searchdata, searchvalue } = this.state
        return (
            <>
                <Helmet><title>Fiscal Year</title></Helmet>

                <React.Fragment>
                <Breadcrumbs
                    breadCrumbTitle="Admin"
                    breadCrumbParent="Setup"
                    breadCrumbActive="Fiscal Year"
                />
                <Row>
                    <Col sm="12">
                        <Card>
                            <CardHeader>
                                <CardTitle>Fiscal Year</CardTitle>
                            </CardHeader>
                            <CardBody className="rdt_Wrapper">
                            <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />
                                <FiscalYearTable
                                    role={this.constructor.name}
                                    handleEdit={this.handleEditData}
                                    handleDelete={this.handleDeleteData}
                                    value={value}
                                    data={data}
                                    columns={columns}
                                    filteredData={filteredData}
                                    handleShow={this.handleShow}
                                    handleFilter={this.handleFilter}
                                />

                                <FiscalYearModal
                                    validated={this.state.validated}
                                    show={this.state.show}
                                    handleHide={this.handleHide}
                                    InsertUpdate={this.state.InsertUpdate}
                                    handleSubmit={this.handleSubmit}
                                    handleShowSearch={this.handleShowSearch}
                                    FiscalYearName={this.state.FISCALYEARNAME}
                                    changeFiscalYearName={e => this.setState({ FISCALYEARNAME: e.target.value })}
                                    selectedStartDate={this.state.STARTDATE !== ''? new Date(this.state.STARTDATE) : ''}
                                    changeStartDate={date => this.setState({ STARTDATE: date})}
                                    selectedEndDate={this.state.ENDDATE !== ''? new Date(this.state.ENDDATE) : ''}
                                    changeEndDate={date => this.setState({ ENDDATE: date})}
                                    selectedDateInitialised={this.state.DATEINITIALISED !== ''? new Date(this.state.DATEINITIALISED) : ''}
                                    changeDateInitialised={date => this.setState({ DATEINITIALISED: date})}
	                                selectedDateClosed={this.state.DATECLOSED !== ''? new Date(this.state.DATECLOSED) : ''}
                                    changeDateClosed={date => this.setState({ DATECLOSED: date})}
	                                selectedInitialisedbyId={this.state.INITIALISEDBYID}
                                    changeInitialisedbyId={e => this.setState({ INITIALISEDBYID: e.target.value })}
                                    selectedClosedBy={this.state.CLOSEDBYID}
                                    changeClosedBy={e => this.setState({ CLOSEDBYID: e.target.value })}
                                    selectedCurrentYear={this.state.CURRENTYEAR}
                                    changeCurrentYear={e => this.setState({ CURRENTYEAR: e.target.value })}
                                    selectedOpened={this.state.OPENED}
                                    changeOpened={e => this.setState({ OPENED: e.target.value })}
                                    selectedNoOfPeriods={this.state.NOOFPERIODS}
                                    changeNoOfPeriods={e => this.setState({ NOOFPERIODS: e.target.value })}
                                    SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                    showdelete={this.state.showdelete}
                                    handleHideDelete={this.handleHideDelete}
                                    handleDelete={this.handleDelete}
                                />


                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </React.Fragment>
        
            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(FiscalYear));